import React from 'react';
import './style.scss';
import Container from "@material-ui/core/Container";

export default function AboutUs({title, dividedParagraph, priorities, language}){

    return <div className="home-about-us-3">
        <h1>{title[language]}</h1>
        <div>
            {
                dividedParagraph[language].map((e, index) =>  <p key={index}>{e} </p>)
            }
        </div>

        <Container maxWidth={'lg'}>
            <ul>
                {
                    priorities[language].map((e, index) =>  <li key={index}>{e} </li>)
                }
            </ul>
        </Container>

    </div>
}
